:root {
  --border-color: rgb(57, 32, 97);
  --background-color: rgb(248, 240, 227);
  --highlight: rgb(251, 100, 190);
  --mid: rgb(125, 140, 196);
}

@media screen and (max-width: 600px) {
  .FlatList {
    display: grid;
    grid-template-columns: 100%;
    grid-auto-flow: row;
    padding-bottom: 3%;
  }
  img {
    width: 90%;
  }
  .navBar button {
    width: 40%;
    margin: 3% 10% 3% 10%;
  }
}

/* styles for devices with a minimum width of 601px and a maximum width of 1200px */
@media screen and (min-width: 601px) and (max-width: 1200px) {
  .FlatList {
    padding-top: 5%;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-auto-flow: row;
    padding-bottom: 3%;
  }
  img {
    width: 80%;
  }
  .navBar button {
    margin: 0 2% 0 2%;
  }
}

/* styles for devices with a minimum width of 1201px */
@media screen and (min-width: 1201px) {
  .FlatList {
    padding-top: 5%;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-auto-flow: row;
    padding-bottom: 3%;
  }
  img {
    width: 50%;
  }
  .Header {
    height: 100vh;
  }
}

.TransPride {
  min-height: 100vh;
}

.AboutUs {
  background-color: var(--mid);
  min-height: 100vh;
}

.AboutUs H1 {
  color: var(--background-color);
  padding-top: 5%;
}

.AboutText {
  border: solid var(--border-color) 2px;
  border-radius: 20px;
  margin: 5%;
  padding: 5%;
  background-color: var(--background-color);
  text-align: center;
}

html {
  background-color: var(--background-color);
}

.HeaderComponent {
  background-color: var(--mid);
  font-size: calc(10px + 2vmin);
  color: var(--background-color);
  text-align: center;
  padding-top: 1%;
  padding-bottom: 1%;
  border: solid var(--border-color) 2px;
}

.BlogHeader {
  color: var(--highlight);
}

.SingleFlat {
  border: solid var(--border-color) 2px;
  border-radius: 20px;
  margin: 5%;
  background-color: var(--background-color);
  text-align: center;
}

.SingleFlat p {
  color: var(--border-color);
  padding: 0 2% 0 2%;
}

h1 {
  color: var(--border-color);
  text-align: center;
  font-family: "bungee", sans-serif;
  font-weight: 400;
  font-style: normal;
}
select {
  border: solid var(--border-color) 2px;
  border-radius: 20px;
  margin: 5%;
  background-color: var(--background-color);
  text-align: center;
  color: var(--highlight);
  padding: 1%;
  margin-left: 1%;
}
select:hover {
  background-color: var(--mid);
  color: var(--background-color);
  cursor: pointer;
}

.Dropdown {
  color: var(--border-color);
  text-align: center;
}

.FlatName {
  align-content: center;
  text-decoration: underline;
}

.FlatDetail {
  text-align: left;
}

img {
  height: auto;
  border: solid var(--highlight) 2px;
  border-radius: 20px;
  margin: 5% 5% 0% 5%;
  box-shadow: 0 0 0 3px hsl(324, 95%, 69%), 0 0 0 6px hsl(324, 95%, 79%),
    0 0 0 9px hsl(324, 95%, 89%);
}

.Header {
  background-color: var(--mid);
  font-size: calc(10px + 2vmin);
  color: var(--background-color);
  text-align: center;
  padding-top: 1%;
  padding-bottom: 1%;
  border: solid var(--border-color) 2px;
}

button {
  background-color: var(--background-color);
  color: var(--highlight);
  border: solid var(--border-color) 2px;

  border-radius: 20px;
  padding: 1%;
  margin: 1%;
  font-weight: 700;
  text-align: center;
  align-self: center;
  font-size: large;
  font-family: "bungee", sans-serif;
  font-weight: 400;
  font-style: normal;
}
.navBar {
  margin-top: 5%;
  margin-bottom: 3%;
}

.navBar button {
  box-shadow: 0 0 0 3px hsl(324, 95%, 69%), 0 0 0 6px hsl(324, 95%, 79%),
    0 0 0 9px hsl(324, 95%, 89%);
}

.BlogButton {
  position: absolute;
  background-color: var(--mid);
  color: var(--background-color);
}
.AboutUsButton {
  position: absolute;
  color: var(--mid);
}

.PanicButton a {
  color: var(--highlight);
  text-decoration: none;
}

#jump {
  position: relative;
  animation-name: jump;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes jump {
  0%,
  50%,
  100% {
    top: 5px;
  }
  10% {
    top: -10px;
  }
}
